@tailwind base;
@tailwind components;
@tailwind utilities;

#container {
  max-width: 400px;
  overflow: hidden;
}

#calendar-table {
  font-family: 'IRANYekan', sans-serif;
  border-collapse: collapse;
  width: 100%;
  background-color: white;
}

#calendar-table th {
  background-color: white;
}

#calendar-table td {
  box-sizing: border-box !important;
  text-align: center;
  cursor: pointer;
  padding: 8px;
}

#calendar-table td span {
  display: block;
  font-family: 'IRANYekan', sans-serif !important;
  unicode-bidi: embed;
}

#calendar-table .current-day {
  background-color: white;
  font-weight: bold;
}

#calendar-table .in-month-day {
  background-color: white;
  color: black
}

#calendar-table .out-month-day {
  background-color: white;
  color: rgb(168, 167, 167)
}

.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: white;
  color: black;
}

  .calendar-header button {
    background-color: transparent;
    color: black;
    cursor: pointer;
  }
  
  .calendar-header h2 {
    margin: 0;
  }
  #calendar-table td.enable-hover-select:hover {
    @apply outline outline-2 outline-blue-500 rounded-lg
  }

  td.selected-day {
    background-color: rgb(51, 153, 187) !important;
    border-radius: 10px;
  }

  table {
    user-select: none;
  }

  h2 {
    user-select: none;
  }